import { useRef, useState } from "react";
import "./styles.css";

export default function App() {
  const [inState, setInState] = useState("");
  const [curseProtection, setCurseProtection] = useState(false);

  const handleChange = (e, enabled) => {
    let s = e?.target?.value ?? e;
    if (curseProtection || enabled) {
      s = s.replace(/(f.ck)|(fu.k)|(f[aeiou]k)/i, "f##k");
      s = s.replace(/(as*h.l.)/i, "ah#le");
      s = s.replace(/(m.+dh*.+r*ch[o0a]+d)/i, "macho");
      s = s.replace(/(\w)*ch[o0a]+d/i, "(no-ch@d-word)");
    }
    setInState(s);
  };

  const toggleCursrProtection = () => {
    setCurseProtection((prev) => !prev);
    handleChange(inState, true);
  };

  return (
    <div className="App">
      <h1 className={"curseHeading"}>You Cannot Curse Here</h1>
      <p>
        {curseProtection
          ? "Now Try Typing: Fucking Asshole"
          : "Curse Protection Disbaled, You can type anything for now!"}
      </p>
      <button
        onClick={toggleCursrProtection}
        className={`protectionButton ${
          curseProtection ? "enabled" : "disabled"
        }`}
      >
        {curseProtection ? "Disable" : "Enable"} Curse Protection
      </button>
      <div>
        <input
          type="text"
          name="controlled"
          value={inState}
          onChange={handleChange}
          placeHolder="Try Cursing Here"
          className={`curseInput ${
            curseProtection ? "protected" : "unprotected"
          }`}
        />
      </div>
      <p className="info">
        This site uses Regex and Controlled input in ReactJs to filter some most
        common curses in English and Hindi (Hinglish)!
      </p>
    </div>
  );
}
